:root {
  --background-texture: url('/public/concrete-wall.png');
  --background-color: #101112;
  --slide-background-color: black;
}

* {
  user-select: none;
}

body {
  background-color: black;
  /* background-image:
    radial-gradient(circle at 50% 100%, rgba(70, 85, 110, 0.5) 0%, transparent 60%),
    radial-gradient(circle at 50% 100%, whitesmoke 0%, transparent 40%),
    radial-gradient(circle at 50% 100%, rgba(181, 184, 208, 0.3) 0%, transparent 80%); */

  background-size: auto;
  color: white;
  height: 100vh;
  overflow:hidden;
}

.scrollbar-white-rounded {
  scrollbar-width: thin;
  scrollbar-color: white transparent; /* Firefox */
}

.scrollbar-white-rounded::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

.scrollbar-white-rounded::-webkit-scrollbar-track {
  background: transparent;
}


.scrollbar-white-rounded::-webkit-scrollbar-thumb {
  background-color: white;
  border-radius: 8px; /* fully rounded */
  border: 3px solid transparent; /* spacing */
  background-clip: content-box;
  min-height: 40px; /* ensures thumb is tall enough to show round ends */
}

@media (max-width: 600px) {
    .mobile-slot-animation-hidden {
        display: none
    }

    .mobile-app-header {
        grid: unset;

    }
}
