.embla {
  margin: auto;
  --slide-height: 19rem;
  --slide-spacing: 1rem;
  --slide-size: 60%;
  --slide-size-mobile: 95%
}
@media (max-width: 1100px) {
  .embla {
    --slide-size: 85% !important;
  }
}

@media (max-width: 800px) {
  .embla {
    --slide-size: 95% !important;
  }
}

.embla {
  margin: auto;
  --slide-height: 19rem;
  --slide-spacing: 1rem;
  --slide-size: 60%;
}
.embla__viewport {
    margin-top: 1em;
  /* overflow: hidden; */
}
.embla__container {
  display: flex;
  touch-action: pan-y pinch-zoom;
  margin-left: calc(var(--slide-spacing) * -1);
}
.embla__slide {
  transform: translate3d(0, 0, 0);
  flex: 0 0 var(--slide-size);
  min-width: 0;
  padding-left: var(--slide-spacing);
}

.embla_slide_content {
    background-color: var(--slide-background-color);
    background-image: var(--background-texture);
    height: calc(var(--vh, 1vh) * 90);
    overflow-y: auto;
    border-radius: 16px;
    animation: shadows 3s ease-in-out infinite;
    z-index: 100;
}

@keyframes shadows {
  0%   { box-shadow: 0 0 10px 3px rgba(245, 245, 245, 0.5); }
  5%   { box-shadow: 0 0 15px 4px rgba(245, 245, 245, 0.5); }
  10%  { box-shadow: 0 0 20px 5px rgba(245, 245, 245, 0.5); }
  15%  { box-shadow: 0 0 25px 6px rgba(245, 245, 245, 0.5); }
  20%  { box-shadow: 0 0 30px 7px rgba(245, 245, 245, 0.5); }
  25%  { box-shadow: 0 0 35px 8px rgba(245, 245, 245, 0.5); }
  30%  { box-shadow: 0 0 40px 9px rgba(245, 245, 245, 0.5); }
  35%  { box-shadow: 0 0 45px 10px rgba(245, 245, 245, 0.5); }
  40%  { box-shadow: 0 0 50px 11px rgba(245, 245, 245, 0.5); }
  45%  { box-shadow: 0 0 55px 12px rgba(245, 245, 245, 0.5); }
  50%  { box-shadow: 0 0 60px 13px rgba(245, 245, 245, 0.5); }
  55%  { box-shadow: 0 0 55px 12px rgba(245, 245, 245, 0.5); }
  60%  { box-shadow: 0 0 50px 11px rgba(245, 245, 245, 0.5); }
  65%  { box-shadow: 0 0 45px 10px rgba(245, 245, 245, 0.5); }
  70%  { box-shadow: 0 0 40px 9px rgba(245, 245, 245, 0.5); }
  75%  { box-shadow: 0 0 35px 8px rgba(245, 245, 245, 0.5); }
  80%  { box-shadow: 0 0 30px 7px rgba(245, 245, 245, 0.5); }
  85%  { box-shadow: 0 0 25px 6px rgba(245, 245, 245, 0.5); }
  90%  { box-shadow: 0 0 20px 5px rgba(245, 245, 245, 0.5); }
  95%  { box-shadow: 0 0 15px 4px rgba(245, 245, 245, 0.5); }
  100% { box-shadow: 0 0 10px 3px rgba(245, 245, 245, 0.5); }
}





.embla_slide_content::after {
    filter: blur(30px);
}

.embla__slide__number {
  box-shadow: inset 0 0 0 0.2rem var(--detail-medium-contrast);
  border-radius: 1.8rem;
  font-size: 4rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--slide-height);
  user-select: none;
}
.embla__controls {
  display: grid;
  grid-template-columns: auto 1fr;
  justify-content: space-between;
  gap: 1.2rem;
  margin-top: 1.8rem;
}
.embla__buttons {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.6rem;
  align-items: center;
}
.embla__button {
  -webkit-tap-highlight-color: rgba(var(--text-high-contrast-rgb-value), 0.5);
  -webkit-appearance: none;
  appearance: none;
  background-color: transparent;
  touch-action: manipulation;
  display: inline-flex;
  text-decoration: none;
  cursor: pointer;
  border: 0;
  padding: 0;
  margin: 0;
  box-shadow: inset 0 0 0 0.2rem var(--detail-medium-contrast);
  width: 3.6rem;
  height: 3.6rem;
  z-index: 1;
  border-radius: 50%;
  color: var(--text-body);
  display: flex;
  align-items: center;
  justify-content: center;
}
.embla__button:disabled {
  color: var(--detail-high-contrast);
}
.embla__button__svg {
  width: 35%;
  height: 35%;
}
.embla__dots {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  margin-right: calc((2.6rem - 1.4rem) / 2 * -1);
}
.embla__dot {
  -webkit-tap-highlight-color: rgba(var(--text-high-contrast-rgb-value), 0.5);
  -webkit-appearance: none;
  appearance: none;
  background-color: transparent;
  touch-action: manipulation;
  display: inline-flex;
  text-decoration: none;
  cursor: pointer;
  border: 0;
  padding: 0;
  margin: 0;
  width: 2.6rem;
  height: 2.6rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.embla__dot:after {
  box-shadow: inset 0 0 0 0.2rem var(--detail-medium-contrast);
  width: 1.4rem;
  height: 1.4rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  content: '';
}
.embla__dot--selected:after {
  box-shadow: inset 0 0 0 0.2rem var(--text-body);
}
.embla__slide__number {
  backface-visibility: hidden;
}
